import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faPlus,
  faEye,
  faPenToSquare,
  faTrash,
  faCircleExclamation,
  faThumbtack,
  faEyeSlash,
  faMagnifyingGlass
} from '@fortawesome/free-solid-svg-icons';

const noticeStats = [
  { label: ' ', value: 2 },
  { label: ' ', value: 1, icon: <FontAwesomeIcon icon={faCircleExclamation} className="text-[var(--red500)]" /> },
  { label: ' ', value: 1, icon: <FontAwesomeIcon icon={faThumbtack} className="text-[var(--primaryBlue)]" /> },
];

const notices = [
  {
    id: 1,
    title: '5   ',
    isNew: true,
    isFixed: true,
    priority: '',
    status: '',
    views: 234,
    date: '2024-04-28',
  },
  {
    id: 2,
    title: '  ',
    isNew: false,
    isFixed: false,
    priority: '',
    status: '',
    views: 156,
    date: '2024-04-25',
  },
];

export default function Notice() {
  const [search, setSearch] = useState('');
  const [filter, setFilter] = useState('');

  const filtered = notices.filter(n =>
    (!search || n.title.includes(search)) &&
    (filter === '' || (filter === '' && n.priority === '') || (filter === '' && n.isFixed))
  );

  return (
    <div className="flex-1 flex flex-col min-h-screen bg-[var(--bgSecondary)]">
      {/*   +   (   ) */}
      <div className="flex items-center justify-between px-10 pt-10 pb-2 mb-6">
        <h1 className="text-3xl font-bold text-[var(--contentMain)]"> </h1>
        <button className="button-primary-m flex items-center gap-1 px-5 py-3 whitespace-nowrap"><FontAwesomeIcon icon={faPlus} className="w-5 h-5" />  </button>
      </div>
      {/*    ,   4 */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 px-10 mb-8 w-full">
        {noticeStats.map((stat, idx) => (
          <div key={stat.label} className="bg-white rounded-[var(--radius-m)] shadow-sm p-6 flex flex-col items-end md:items-start border border-[var(--borderOutline)] min-h-[80px]">
            <div className="flex items-center gap-1 text-[var(--contentCaption)] text-sm font-semibold w-full justify-between">
              <span>{stat.label}</span>
              {stat.icon}
            </div>
            <div className={`text-2xl font-bold mt-1 w-full ${stat.label === ' ' ? 'text-[var(--red500)]' : 'text-[var(--contentMain)]'}`}>{stat.value}</div>
          </div>
        ))}
      </div>
      {/*    */}
      <div className="bg-white rounded-[var(--radius-l)] shadow-sm border border-[var(--borderOutline)] mx-10 p-8">
        <div className="font-bold text-[var(--contentMain)] mb-1 text-lg"> </div>
        <div className="text-sm text-[var(--contentCaption)] mb-4">     .</div>
        <div className="flex gap-2 items-center mb-4">
          <div className="relative flex-1 max-w-xs">
            <input type="text" className="w-full pl-9 pr-3 py-2 rounded border border-[var(--borderInput)] bg-white text-sm" placeholder=" ..." value={search} onChange={e => setSearch(e.target.value)} />
            <FontAwesomeIcon icon={faMagnifyingGlass} className="absolute left-2 top-1/2 -translate-y-1/2 text-[var(--contentCaption)]" />
          </div>
          <select className="px-3 py-2 rounded border border-[var(--borderInput)] bg-[var(--bgPrimary)] text-sm" value={filter} onChange={e => setFilter(e.target.value)} style={{width:100}}>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <table className="w-full text-sm">
          <thead>
            <tr className="bg-[var(--bgTertiary)]">
              <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-left"></th>
              <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
              <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
              <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
              <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
              <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
            </tr>
          </thead>
          <tbody>
            {filtered.map(n => (
              <tr key={n.id} className="border-b last:border-b-0">
                <td className="py-3 px-2 text-left font-medium text-[var(--contentMain)]">
                  {n.title}
                  {n.isNew && <span className="ml-2 bg-[var(--red100)] text-[var(--red500)] text-xs font-bold px-2 py-0.5 rounded">NEW</span>}
                  {n.isFixed && <span className="ml-2 bg-[var(--bgTertiary)] text-[var(--contentCaption)] text-xs font-bold px-2 py-0.5 rounded"></span>}
                </td>
                <td className="py-3 px-2 text-center">
                  <span className={`px-2 py-1 rounded text-xs font-bold ${n.priority === '' ? 'bg-[var(--red100)] text-[var(--red500)]' : 'bg-[var(--blue100)] text-[var(--primaryBlue)]'}`}>{n.priority}</span>
                </td>
                <td className="py-3 px-2 text-center">
                  <span className="px-2 py-1 rounded text-xs font-bold bg-[var(--green100)] text-[var(--green500)]">{n.status}</span>
                </td>
                <td className="py-3 px-2 text-center">{n.views}</td>
                <td className="py-3 px-2 text-center">{n.date}</td>
                <td className="py-3 px-2 text-center">
                  <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faEye} className="w-4 h-4" /></button>
                  <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faPenToSquare} className="w-4 h-4" /></button>
                  <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faTrash} className="w-4 h-4" /></button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
} 