import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faDownload,
  faQrcode,
  faMagnifyingGlass,
  faCheck,
  faXmark,
  faEye
} from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';

const regularReservations = [
  { id: 1, user: '', date: '2024-01-15', meal: '', status: '', qr: true, time: '12:30' },
  { id: 2, user: '', date: '2024-01-15', meal: '', status: '', qr: false, time: '-' },
];

const additionalReservations = [
  { id: 1, user: '', date: '2024-01-15', meal: '', count: 2, reason: '     ', status: '', qr: false },
  { id: 2, user: '', date: '2024-01-15', meal: '', count: 1, reason: '     ', status: '', qr: false },
  { id: 3, user: '', date: '2024-01-15', meal: '', count: 3, reason: '  ', status: '', qr: true },
];

//   /  
const regularLunchCount = regularReservations.filter(r => r.meal === '').length;
const regularDinnerCount = regularReservations.filter(r => r.meal === '').length;
//   /  
const additionalLunchCount = additionalReservations.filter(r => r.meal === '').length;
const additionalDinnerCount = additionalReservations.filter(r => r.meal === '').length;

const customTabs = [
  { label: `  (${regularReservations.length})`, value: 0 },
  { label: `  (${additionalReservations.length})`, value: 1 },
];

const statCards = [
  { label: ' ', value: 45 },
  { label: ' ', value: 38 },
  { label: ' ', value: `${regularLunchCount} / ${regularDinnerCount}` },
  { label: ' ', value: `${additionalLunchCount} / ${additionalDinnerCount}` },
  { label: 'QR ', value: 8 },
];

export default function Reservations() {
  const [tab, setTab] = useState(0);
  const [search, setSearch] = useState('');
  const [mealFilter, setMealFilter] = useState('');
  const [statusFilter, setStatusFilter] = useState('');
  const navigate = useNavigate();

  const filtered = (tab === 0 ? regularReservations : additionalReservations).filter(r =>
    (!search || r.user.includes(search)) &&
    (mealFilter === '' || r.meal === mealFilter) &&
    (statusFilter === '' || r.status === statusFilter)
  );

  return (
    <div className="flex-1 flex flex-col min-h-screen bg-[var(--bgSecondary)]">
      {/*   + / */}
      <div className="flex items-center justify-between px-10 pt-10 pb-4">
        <h1 className="text-3xl font-bold text-[var(--contentMain)]">  </h1>
        <div className="flex gap-2 min-w-[420px]">
          <select className="px-3 py-2 rounded border border-[var(--borderInput)] bg-[var(--bgPrimary)] text-sm" style={{width:120}} defaultValue="">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          <button className="button-tertiary-m flex items-center gap-1 px-4 py-2 border border-[var(--borderOutline)]">
            <FontAwesomeIcon icon={faDownload} className="w-5 h-5" />   
          </button>
          <button className="button-primary-m flex items-center gap-1 px-4 py-2" onClick={() => navigate('/qr')}><FontAwesomeIcon icon={faQrcode} className="w-5 h-5" /> QR  </button>
        </div>
      </div>
      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-5 gap-5 px-10 pb-6">
        {statCards.map((card, idx) => (
          <div key={card.label} className="bg-white rounded-[var(--radius-m)] shadow-sm p-6 flex flex-col items-center border border-[var(--borderOutline)] min-h-[80px]">
            <div className="text-[var(--contentCaption)] text-sm">{card.label}</div>
            <div className={`text-2xl font-bold mt-1 ${card.label.includes('') ? 'text-[var(--primaryBlue)]' : card.label.includes('') ? 'text-[var(--yellow500)]' : card.label.includes('QR') ? 'text-[var(--red500)]' : 'text-[var(--contentMain)]'}`}>{card.value}</div>
          </div>
        ))}
      </div>
      {/*   */}
      <div className="flex justify-center gap-2 mb-3 px-10">
        {customTabs.map(tabItem => (
          <button
            key={tabItem.value}
            onClick={() => setTab(tabItem.value)}
            className={`flex-1 h-11 rounded-[var(--radius-s)] text-base transition font-semibold border ${tab===tabItem.value ? 'bg-[var(--bgPrimary)] border-[var(--contentMain)] shadow-sm text-[var(--contentMain)]' : 'bg-transparent border-transparent text-[var(--contentSub)]'}`}
            style={{minWidth:160}}
          >
            {tabItem.label}
          </button>
        ))}
      </div>
      {/*     */}
      <div className="bg-white rounded-[var(--radius-m)] shadow-sm p-6 border border-[var(--borderOutline)] mx-10 mb-10">
        <div className="font-bold text-[var(--contentMain)] mb-1">{tab === 0 ? '  ' : '  '}</div>
        <div className="text-sm text-[var(--contentCaption)] mb-3">{tab === 0 ? '      .' : '      .'}</div>
        {/* / */}
        <div className="flex gap-2 items-center mb-4">
          <div className="relative">
            <input type="text" className="pl-9 pr-3 py-2 rounded border border-[var(--borderInput)] bg-white text-sm" style={{width:220}} placeholder=" ..." value={search} onChange={e => setSearch(e.target.value)} />
            <FontAwesomeIcon icon={faMagnifyingGlass} className="absolute left-2 top-1/2 -translate-y-1/2 text-[var(--contentCaption)]" />
          </div>
          <select className="px-3 py-2 rounded border border-[var(--borderInput)] bg-[var(--white)] text-sm" value={mealFilter} onChange={e => setMealFilter(e.target.value)} style={{width:100}}>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          <select className="px-3 py-2 rounded border border-[var(--borderInput)] bg-[var(--white)] text-sm" value={statusFilter} onChange={e => setStatusFilter(e.target.value)} style={{width:100}}>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        {/*  */}
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead>
              <tr className="bg-[var(--bgTertiary)]">
                <th className="font-bold text-[var(--contentMain)] py-2 text-center"></th>
                <th className="font-bold text-[var(--contentMain)] py-2 text-center"></th>
                <th className="font-bold text-[var(--contentMain)] py-2 text-center"></th>
                {tab === 1 && <th className="font-bold text-[var(--contentMain)] py-2 text-center"> </th>}
                {tab === 1 && <th className="font-bold text-[var(--contentMain)] py-2 text-center"> </th>}
                <th className="font-bold text-[var(--contentMain)] py-2 text-center"></th>
                <th className="font-bold text-[var(--contentMain)] py-2 text-center">QR </th>
                {tab === 0 && <th className="font-bold text-[var(--contentMain)] py-2 text-center"> </th>}
                {tab === 1 && <th className="font-bold text-[var(--contentMain)] py-2 text-center"></th>}
              </tr>
            </thead>
            <tbody>
              {filtered.map(r => (
                <tr key={r.id} className="border-b last:border-b-0">
                  <td className="py-2 font-bold text-[var(--contentMain)] text-center">{r.user}</td>
                  <td className="py-2 text-center">{r.date}</td>
                  <td className="py-2 text-center">{r.meal}</td>
                  {tab === 1 && <td className="py-2 text-center text-[var(--yellow700)] font-bold">+{r.count}</td>}
                  {tab === 1 && <td className="py-2 text-center">{r.reason}</td>}
                  <td className="py-2 text-center">
                    <span className={`px-2 py-1 rounded text-xs font-bold ${r.status === '' ? 'bg-[var(--green100)] text-[var(--green500)]' : 'bg-[var(--bgTertiary)] text-[var(--contentCaption)]'}`}>{r.status}</span>
                  </td>
                  <td className="py-2 text-center">
                    {r.qr ? <FontAwesomeIcon icon={faCheck} className="inline text-[var(--primaryBlue)]" /> : <FontAwesomeIcon icon={faXmark} className="inline text-[var(--red500)]" />}
                  </td>
                  {tab === 0 && <td className="py-2 text-center">{r.time}</td>}
                  {tab === 1 && (
                    <td className="py-2 text-center">
                      <div className="flex justify-center gap-2">
                        <button className="px-3 py-1 rounded bg-[var(--primaryBlue)] text-white text-xs font-semibold hover:bg-[var(--blue700)] transition"></button>
                        <button className="px-3 py-1 rounded bg-[var(--red100)] text-[var(--red500)] text-xs font-semibold hover:bg-[var(--red200)] transition"></button>
                      </div>
                    </td>
                  )}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
} 