import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faHeart,
  faCheck,
  faUpload,
  faEye,
  faPenToSquare,
  faTrash,
  faPlus,
  faDownload,
  faMagnifyingGlass
} from '@fortawesome/free-solid-svg-icons';

const sponsorStats = [
  { label: ' ', value: 4, icon: <FontAwesomeIcon icon={faHeart} className="text-[var(--contentCaption)]" /> },
  { label: ' ', value: 2, icon: <FontAwesomeIcon icon={faUpload} className="text-[var(--contentCaption)]" /> },
];

const posts = [
  { id: 1, title: '1   !  ', isNew: true, isFixed: true, category: '', author: '', status: '', views: 342, date: '2024-01-15' },
  { id: 2, title: '    ', isNew: false, isFixed: false, category: '', author: '', status: '', views: 156, date: '2024-01-10' },
  { id: 3, title: '   ', isNew: false, isFixed: true, category: '', author: '', status: '', views: 289, date: '2024-01-08' },
  { id: 4, title: '2    ', isNew: false, isFixed: false, category: '', author: '', status: '', views: 89, date: '2024-01-05' },
];

const previewPosts = [
  { id: 1, title: '1   !  ', isNew: true, isFixed: true, content: '    1     .       .', category: '', views: 342, date: '2024-01-15' },
  { id: 2, title: '    ', isNew: false, isFixed: false, content: '     .  ,      .', category: '', views: 156, date: '2024-01-10' },
  { id: 3, title: '   ', isNew: false, isFixed: true, content: '      .        .', category: '', views: 289, date: '2024-01-08' },
];

//   ,   
const sortedPosts = [...posts].sort((a, b) => {
  if (a.isFixed && !b.isFixed) return -1;
  if (!a.isFixed && b.isFixed) return 1;
  return new Date(b.date) - new Date(a.date);
});

export default function Sponser() {
  const [search, setSearch] = useState('');
  const [category, setCategory] = useState('');
  const [status, setStatus] = useState('');

  const filtered = sortedPosts.filter(
    p => (!search || p.title.includes(search)) &&
      (category === '' || p.category === category) &&
      (status === '' || p.status === status)
  );

  return (
    <div className="flex-1 flex flex-col min-h-screen bg-[var(--bgSecondary)]">
      {/*   +  */}
      <div className="flex items-center justify-between px-10 pt-10 pb-2 mb-6">
        <h1 className="text-3xl font-bold text-[var(--contentMain)]">  </h1>
        <div className="flex gap-2">
          <button className="button-tertiary-m flex items-center gap-1 px-4 py-2 border border-[var(--borderOutline)]"><FontAwesomeIcon icon={faDownload} className="w-5 h-5" />  </button>
          <button className="button-primary-m flex items-center gap-1 px-4 py-2"><FontAwesomeIcon icon={faPlus} className="w-5 h-5" />   </button>
        </div>
      </div>
      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 px-10 mb-8 w-full">
        {sponsorStats.map(stat => (
          <div key={stat.label} className="bg-white rounded-[var(--radius-m)] shadow-sm p-6 flex flex-col items-end md:items-start border border-[var(--borderOutline)] min-h-[80px]">
            <div className="flex items-center gap-1 text-[var(--contentCaption)] text-sm font-semibold w-full justify-between">
              <span>{stat.label}</span>
              {stat.icon}
            </div>
            <div className="text-2xl font-bold mt-1 w-full text-[var(--contentMain)]">{stat.value}</div>
          </div>
        ))}
      </div>
      {/*    */}
      <div className="bg-white rounded-[var(--radius-l)] shadow-sm border border-[var(--borderOutline)] mx-10 p-8 mb-6">
        <div className="font-bold text-[var(--contentMain)] mb-1 text-lg">  </div>
        <div className="text-sm text-[var(--contentCaption)] mb-4">        .</div>
        <div className="flex gap-2 items-center mb-4">
          <div className="relative flex-1 max-w-xs">
            <input type="text" className="w-full pl-9 pr-3 py-2 rounded border border-[var(--borderInput)] bg-white text-sm" placeholder=" ..." value={search} onChange={e => setSearch(e.target.value)} />
            <FontAwesomeIcon icon={faMagnifyingGlass} className="absolute left-2 top-1/2 -translate-y-1/2 text-[var(--contentCaption)]" />
          </div>
          <select className="px-3 py-2 rounded border border-[var(--borderInput)] bg-[var(--bgPrimary)] text-sm" value={category} onChange={e => setCategory(e.target.value)} style={{width:100}}>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          <select className="px-3 py-2 rounded border border-[var(--borderInput)] bg-[var(--bgPrimary)] text-sm" value={status} onChange={e => setStatus(e.target.value)} style={{width:100}}>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead>
              <tr className="bg-[var(--bgTertiary)]">
                <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-left"></th>
                <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
                <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
                <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
                <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
                <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
              </tr>
            </thead>
            <tbody>
              {filtered
                .sort((a, b) => {
                  if (a.isFixed && !b.isFixed) return -1;
                  if (!a.isFixed && b.isFixed) return 1;
                  return new Date(b.date) - new Date(a.date);
                })
                .map(p => (
                  <tr key={p.id} className="border-b last:border-b-0">
                    <td className="py-3 px-2 text-left font-medium text-[var(--contentMain)]">
                      {p.title}
                      {p.isNew && <span className="ml-2 bg-[var(--red100)] text-[var(--red500)] text-xs font-bold px-2 py-0.5 rounded">NEW</span>}
                      {p.isFixed && <span className="ml-2 bg-[var(--bgTertiary)] text-[var(--contentCaption)] text-xs font-bold px-2 py-0.5 rounded"></span>}
                    </td>
                    <td className="py-3 px-2 text-center">{p.author}</td>
                    <td className="py-3 px-2 text-center">
                      <span className="px-2 py-1 rounded text-xs font-bold bg-[var(--green100)] text-[var(--green500)]">{p.status}</span>
                    </td>
                    <td className="py-3 px-2 text-center">{p.views}</td>
                    <td className="py-3 px-2 text-center">{p.date}</td>
                    <td className="py-3 px-2 text-center">
                      <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faEye} className="w-4 h-4" /></button>
                      <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faPenToSquare} className="w-4 h-4" /></button>
                      <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faTrash} className="w-4 h-4" /></button>
                    </td>
                  </tr>
                ))}
            </tbody>
          </table>
        </div>
      </div>
      {/*     */}
      <div className="bg-white rounded-[var(--radius-l)] shadow-sm border border-[var(--borderOutline)] mx-10 p-8 mb-10">
        <div className="font-bold text-[var(--contentMain)] mb-1 text-lg">  </div>
        <div className="text-sm text-[var(--contentCaption)] mb-4">       .</div>
        <div className="flex flex-col gap-4">
          {previewPosts.map(post => (
            <div key={post.id} className="bg-[var(--bgTertiary)] rounded-[var(--radius-s)] p-5 flex flex-col md:flex-row md:items-center md:justify-between gap-2">
              <div className="flex-1">
                <div className="flex items-center gap-2 mb-1">
                  <span className="font-semibold text-[var(--contentMain)]">{post.title}</span>
                  {post.isNew && <span className="ml-2 bg-[var(--red100)] text-[var(--red500)] text-xs font-bold px-2 py-0.5 rounded">NEW</span>}
                  {post.isFixed && <span className="ml-2 bg-[var(--bgTertiary)] text-[var(--contentCaption)] text-xs font-bold px-2 py-0.5 rounded"></span>}
                </div>
                <div className="text-[var(--contentCaption)] text-sm mb-1 line-clamp-2">{post.content}</div>
                <div className="flex gap-3 text-xs text-[var(--contentCaption)]">
                  <span>{post.category}</span>
                  <span> {post.views}</span>
                  <span> {post.date}</span>
                </div>
              </div>
              <div className="flex gap-2 mt-2 md:mt-0">
                <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faEye} className="w-4 h-4" /></button>
                <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faPenToSquare} className="w-4 h-4" /></button>
                <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faTrash} className="w-4 h-4" /></button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
} 