import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faUtensils,
  faPlus,
  faUpload,
  faSun,
  faCloud,
  faWheatAlt,
  faSnowflake,
  faBowlFood,
  faPenToSquare,
  faXmark,
  faEye
} from '@fortawesome/free-solid-svg-icons';

const todayMenus = [
  {
    type: ' ',
    date: '2024-01-15 | ',
    main: '',
    sides: ', , , ',
    soup: '',
    allergy: ', ',
  },
  {
    type: ' ',
    date: '2024-01-15 | ',
    main: '',
    sides: ', , ',
    soup: '',
    allergy: ', , ',
  },
];

const weekMenus = [
  {
    day: '', date: '2024-01-15',
    lunch: { main: '', type: '', sides: ',  ' },
    dinner: { main: '', type: '', sides: ',  ' },
  },
  {
    day: '', date: '2024-01-16',
    lunch: { main: '', type: '', sides: ',  ' },
    dinner: { main: '', type: '', sides: ',  ' },
  },
  {
    day: '', date: '2024-01-17',
    lunch: { main: '', type: '', sides: ',  ' },
    dinner: { main: '', type: '', sides: ', ' },
  },
  {
    day: '', date: '2024-01-18',
    lunch: { main: '', type: '', sides: ',  ' },
    dinner: { main: '', type: '', sides: ',  ' },
  },
  {
    day: '', date: '2024-01-19',
    lunch: { main: '', type: '', sides: ',  ' },
    dinner: { main: '', type: '', sides: ',  ' },
  },
];

export default function Menu() {
  const [tab, setTab] = useState(0); // 0:  , 1:  
  const [todayMenusState, setTodayMenus] = useState(todayMenus);
  const [weekMenusState, setWeekMenus] = useState(weekMenus);
  const [modalOpen, setModalOpen] = useState(false);
  const [modalType, setModalType] = useState('today'); // 'today' | 'week'
  const [form, setForm] = useState({
    type: ' ',
    date: '',
    main: '',
    sides: '',
    soup: '',
    allergy: '',
  });
  const [weekForm, setWeekForm] = useState([
    { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
    { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
    { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
    { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
    { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
  ]);

  //   
  const handleAddMenu = (e) => {
    e.preventDefault();
    setTodayMenus([
      ...todayMenusState,
      { ...form },
    ]);
    setForm({ type: ' ', date: '', main: '', sides: '', soup: '', allergy: '' });
    setModalOpen(false);
  };
  //    
  const handleAddWeekMenu = (e) => {
    e.preventDefault();
    setWeekMenus([...weekMenusState, ...weekForm]);
    setWeekForm([
      { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
      { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
      { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
      { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
      { day: '', date: '', lunch: { main: '', type: '', sides: '' }, dinner: { main: '', type: '', sides: '' } },
    ]);
    setModalOpen(false);
  };

  return (
    <div className="flex-1 flex flex-col min-h-screen bg-[var(--bgSecondary)]">
      {/*   +  */}
      <div className="flex items-center justify-between px-10 pt-10 pb-4">
        <h1 className="text-3xl font-bold text-[var(--contentMain)]">  </h1>
        <div className="flex gap-2">
          <button className="button-tertiary-m flex items-center gap-1 px-4 py-2 border border-[var(--borderOutline)]"><FontAwesomeIcon icon={faUpload} size="lg" />   </button>
          <button className="button-primary-m flex items-center gap-1 px-4 py-2" onClick={() => { setModalType('today'); setModalOpen(true); }}><FontAwesomeIcon icon={faPlus} size="lg" />  </button>
        </div>
      </div>
      {/*    */}
      {modalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-30">
          <div className="bg-white rounded-lg shadow-lg p-8 w-[90vw] max-w-none flex flex-col gap-4 relative">
            <button className="absolute top-3 right-3 text-gray-400 hover:text-gray-700" onClick={() => setModalOpen(false)}><FontAwesomeIcon icon={faXmark} size="lg" /></button>
            <div className="flex gap-2 mb-2">
              <button className={`flex-1 py-2 rounded ${modalType==='today' ? 'bg-blue-500 text-white' : 'bg-gray-100 text-gray-700'}`} onClick={()=>setModalType('today')}>  </button>
              <button className={`flex-1 py-2 rounded ${modalType==='week' ? 'bg-blue-500 text-white' : 'bg-gray-100 text-gray-700'}`} onClick={()=>setModalType('week')}>  </button>
            </div>
            {modalType === 'today' ? (
              <form className="flex flex-col gap-3" onSubmit={handleAddMenu}>
                <select className="border rounded px-3 py-2 text-sm" value={form.type} onChange={e=>setForm({...form, type: e.target.value})}>
                  <option value=" "> </option>
                  <option value=" "> </option>
                </select>
                <input className="border rounded px-3 py-2 text-sm" placeholder=" (: 2024-01-15 | )" value={form.date} onChange={e=>setForm({...form, date: e.target.value})} required/>
                <input className="border rounded px-3 py-2 text-sm" placeholder="" value={form.main} onChange={e=>setForm({...form, main: e.target.value})} required/>
                <input className="border rounded px-3 py-2 text-sm" placeholder="" value={form.sides} onChange={e=>setForm({...form, sides: e.target.value})} required/>
                <input className="border rounded px-3 py-2 text-sm" placeholder="" value={form.soup} onChange={e=>setForm({...form, soup: e.target.value})}/> 
                <input className="border rounded px-3 py-2 text-sm" placeholder=" " value={form.allergy} onChange={e=>setForm({...form, allergy: e.target.value})}/>
                <div className="flex gap-2 mt-2">
                  <button type="button" className="flex-1 py-2 rounded bg-gray-200 text-gray-700 font-semibold" onClick={()=>setModalOpen(false)}></button>
                  <button type="submit" className="flex-1 py-2 rounded bg-blue-500 text-white font-semibold"></button>
                </div>
              </form>
            ) : (
              <form className="flex flex-col gap-3" onSubmit={handleAddWeekMenu}>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  {weekForm.map((row, idx) => (
                    <div key={row.day} className="border rounded p-3 mb-2">
                      <div className="font-bold mb-1">{row.day}</div>
                      <input className="border rounded px-3 py-2 text-sm mb-1 w-full" placeholder=" (: 2024-01-15)" value={row.date} onChange={e=>{
                        const copy = [...weekForm]; copy[idx].date = e.target.value; setWeekForm(copy);
                      }} required/>
                      <div className="text-xs text-gray-500 mb-1 mt-2"></div>
                      <div className="grid grid-cols-1 md:grid-cols-4 gap-2 mb-1">
                        <input className="border rounded px-3 py-2 text-sm w-full" placeholder=" " value={row.lunch.main} onChange={e=>{ const copy = [...weekForm]; copy[idx].lunch.main = e.target.value; setWeekForm(copy); }} required/>
                        <input className="border rounded px-3 py-2 text-sm w-full" placeholder=" " value={row.lunch.type} onChange={e=>{ const copy = [...weekForm]; copy[idx].lunch.type = e.target.value; setWeekForm(copy); }} required/>
                        <input className="border rounded px-3 py-2 text-sm w-full" placeholder=" " value={row.lunch.sides} onChange={e=>{ const copy = [...weekForm]; copy[idx].lunch.sides = e.target.value; setWeekForm(copy); }} required/>
                      </div>
                      <div className="text-xs text-gray-500 mb-1 mt-2"></div>
                      <div className="grid grid-cols-1 md:grid-cols-4 gap-2">
                        <input className="border rounded px-3 py-2 text-sm w-full" placeholder=" " value={row.dinner.main} onChange={e=>{ const copy = [...weekForm]; copy[idx].dinner.main = e.target.value; setWeekForm(copy); }} required/>
                        <input className="border rounded px-3 py-2 text-sm w-full" placeholder=" " value={row.dinner.type} onChange={e=>{ const copy = [...weekForm]; copy[idx].dinner.type = e.target.value; setWeekForm(copy); }} required/>
                        <input className="border rounded px-3 py-2 text-sm w-full" placeholder=" " value={row.dinner.sides} onChange={e=>{ const copy = [...weekForm]; copy[idx].dinner.sides = e.target.value; setWeekForm(copy); }} required/>
                      </div>
                    </div>
                  ))}
                </div>
                <div className="flex gap-2 mt-2">
                  <button type="button" className="flex-1 py-2 rounded bg-gray-200 text-gray-700 font-semibold" onClick={()=>setModalOpen(false)}></button>
                  <button type="submit" className="flex-1 py-2 rounded bg-blue-500 text-white font-semibold"></button>
                </div>
              </form>
            )}
          </div>
        </div>
      )}
      {/*   */}
      <div className="flex justify-center gap-2 mb-6 px-10">
        <button
          onClick={() => setTab(0)}
          className={`flex-1 h-11 rounded-[var(--radius-s)] text-base transition font-semibold border ${tab===0 ? 'bg-[var(--bgPrimary)] border-[var(--contentMain)] shadow-sm text-[var(--contentMain)]' : 'bg-transparent border-transparent text-[var(--contentSub)]'}`}
          style={{minWidth:160}}
        >
           
        </button>
        <button
          onClick={() => setTab(1)}
          className={`flex-1 h-11 rounded-[var(--radius-s)] text-base transition font-semibold border ${tab===1 ? 'bg-[var(--bgPrimary)] border-[var(--contentMain)] shadow-sm text-[var(--contentMain)]' : 'bg-transparent border-transparent text-[var(--contentSub)]'}`}
          style={{minWidth:160}}
        >
           
        </button>
      </div>
      {/*    */}
      {tab === 0 && (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 px-10 pb-10">
          {todayMenusState.map((menu, idx) => (
            <div key={idx} className="bg-white rounded-[var(--radius-l)] shadow-sm p-8 border border-[var(--borderOutline)] flex flex-col min-h-[320px]">
              <div className="flex items-center gap-2 mb-2">
                <FontAwesomeIcon icon={faUtensils} className="w-5 h-5 text-[var(--contentCaption)]" />
                <span className="font-bold text-lg text-[var(--contentMain)]">{menu.type}</span>
              </div>
              <div className="text-xs text-[var(--contentCaption)] mb-2">{menu.date}</div>
              <div className="border-b border-[var(--borderOutline)] mb-4"></div>
              <div className="mb-2">
                <div className="text-[var(--contentCaption)] text-sm font-medium"></div>
                <div className="text-xl font-bold text-[var(--contentMain)]">{menu.main}</div>
              </div>
              <div className="mb-2">
                <div className="text-[var(--contentCaption)] text-sm font-medium"></div>
                <div className="text-[var(--contentMain)]">{menu.sides}</div>
              </div>
              <div className="mb-2">
                <div className="text-[var(--contentCaption)] text-sm font-medium"></div>
                <div className="text-[var(--contentMain)]">{menu.soup}</div>
              </div>
              <div className="flex justify-between text-sm mt-auto">
                <span>: {menu.allergy}</span>
              </div>
              <div className="flex gap-2 mt-4">
                <button className="px-4 py-2 rounded border border-[var(--borderOutline)] text-[var(--contentMain)] text-sm font-semibold hover:bg-[var(--bgTertiary)] transition"></button>
                <button className="px-4 py-2 rounded border border-[var(--borderOutline)] text-[var(--contentMain)] text-sm font-semibold hover:bg-[var(--bgTertiary)] transition"></button>
              </div>
            </div>
          ))}
        </div>
      )}
      {/*    */}
      {tab === 1 && (
        <div className="px-10 pb-10">
          <div className="flex items-center justify-between mb-4">
            <div className="text-xl font-bold text-[var(--contentMain)]">  <span className="text-base font-normal ml-2 text-[var(--contentCaption)]">2024 1 3</span></div>
            <div className="flex gap-2 items-center">
              <select className="px-3 py-2 rounded border border-[var(--borderInput)] bg-[var(--bgPrimary)] text-sm" style={{width:120}} defaultValue=" ">
                <option value=" "> </option>
                <option value=" "> </option>
                <option value=" "> </option>
              </select>
              <button className="px-4 py-2 rounded border border-[var(--borderOutline)] bg-white text-[var(--contentMain)] text-sm font-semibold flex items-center gap-1 shadow-sm hover:bg-[var(--bgTertiary)] transition"><FontAwesomeIcon icon={faPenToSquare} size="lg" /> <span>  </span></button>
              <button className="px-4 py-2 rounded bg-[var(--primaryBlue)] text-white text-sm font-semibold flex items-center gap-1 shadow-sm" onClick={()=>{ setModalType('week'); setModalOpen(true); }}><FontAwesomeIcon icon={faPlus} size="lg" /> <span>  </span></button>
            </div>
          </div>
          <div className="bg-white rounded-[var(--radius-l)] shadow-sm border border-[var(--borderOutline)] overflow-x-auto">
            <table className="w-full text-sm">
              <thead>
                <tr className="bg-[var(--bgTertiary)]">
                  <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
                  <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
                  <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"> </th>
                  <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"> </th>
                  <th className="py-3 px-2 font-bold text-[var(--contentMain)] text-center"></th>
                </tr>
              </thead>
              <tbody>
                {weekMenusState.map((row, idx) => (
                  <tr key={row.day + row.date + idx} className="border-b last:border-b-0">
                    <td className="py-3 px-2 text-center font-bold text-[var(--contentMain)]">{row.day}</td>
                    <td className="py-3 px-2 text-center text-[var(--contentCaption)]">{row.date}</td>
                    <td className="py-3 px-2">
                      <div className="font-bold text-[var(--contentMain)] flex items-center gap-2">{row.lunch.main} <span className="badge bg-[var(--bgTertiary)] text-[var(--contentCaption)] text-xs px-2 py-0.5 rounded">{row.lunch.type}</span></div>
                      <div className="text-[var(--contentSub)] text-xs">{row.lunch.sides}</div>
                    </td>
                    <td className="py-3 px-2">
                      <div className="font-bold text-[var(--contentMain)] flex items-center gap-2">{row.dinner.main} <span className="badge bg-[var(--bgTertiary)] text-[var(--contentCaption)] text-xs px-2 py-0.5 rounded">{row.dinner.type}</span></div>
                      <div className="text-[var(--contentSub)] text-xs">{row.dinner.sides}</div>
                    </td>
                    <td className="py-3 px-2 text-center">
                      <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faPenToSquare} className="w-4 h-4" /></button>
                      <button className="p-2 hover:bg-[var(--bgTertiary)] rounded" title=""><FontAwesomeIcon icon={faEye} className="w-4 h-4" /></button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  );
} 